import {ControlKeys, ControlCommands} from '../../util/constants'
import {FormControl, InputLabel, Select, MenuItem} from '@material-ui/core'
import { dropdownStyle } from './DropdownStyle'

interface OSDProps{
    handleCommand: (e: ControlKeys) => void
}


const useStyles = dropdownStyle
const id = 'pj_postion'
export default function PJPositon(props: OSDProps){
    const {handleCommand} = props
    const classes = useStyles()
    
    return (
        <>
    <FormControl className={classes.formControl}>
    <InputLabel id={id}>PJ Position</InputLabel>
    <Select labelId={id} id={id}
     value={''} onChange={((e)=> handleCommand(e.target.value as ControlKeys))}>
         <MenuItem key='fa' value={ControlCommands.PROJECTOR_INSTALL_METHOD_FRONT_AUTO}>Front/Auto</MenuItem>
         <MenuItem key='fc' value={ControlCommands.PROJECTOR_INSTALL_METHOD_FRONT_CEILING}>Front/Ceiling</MenuItem>
         <MenuItem key='fd' value={ControlCommands.PROJECTOR_INSTALL_METHOD_FRONT_DESK}>Front/Desk</MenuItem>
         <MenuItem key='ra' value={ControlCommands.PROJECTOR_INSTALL_METHOD_REAR_AUTO}>Rear/Auto</MenuItem>
         <MenuItem key='rc' value={ControlCommands.PROJECTOR_INSTALL_METHOD_REAR_CEILING}>Rear/Ceiling</MenuItem>
         <MenuItem key='rd' value={ControlCommands.PROJECTOR_INSTALL_METHOD_REAR_DESK}>Rear/Desk</MenuItem>
     </Select>
     
</FormControl>
</> )
}