import { Box, Slider,Typography } from "@material-ui/core";
import { ChangeEvent, useState } from "react";
import { ControlKeys, HandleCommand } from "../../util/constants";

interface slider_props{
    handleCommand: HandleCommand
    cmd: ControlKeys
    min: number
    max: number
    label: string
}
export default function QuickSlider(props: slider_props){
    const {handleCommand,cmd, min,max, label}=props
    const [value, setValue] =useState(max)
    const handleChange=(event: ChangeEvent<{}>, newValue: number | number[])=>{
        console.log('sliddder',newValue)
        setValue(newValue as number)
        handleCommand(cmd,newValue+'')

    }
    return(
    <Box style={{width:200}}>
        <Typography id="input-slider" gutterBottom>
        {label}
      </Typography>
        <Slider value={value} min={min} max={max} onChange={handleChange}/>
    </Box>

    )
}