import { ControlKeys, ControlCommands } from '../../util/constants'
import { FormControl, InputLabel, Select, MenuItem, } from '@material-ui/core'
import { dropdownStyle } from './DropdownStyle'

interface TestPatterProps {
    handleCommand: (e: ControlKeys) => void
}


const useStyles = dropdownStyle
export default function Testpattern(props: TestPatterProps) {
    const { handleCommand } = props
    const classes = useStyles()

    return (
        <>
            <FormControl className={classes.formControl}>
                <InputLabel id='test_pattren'>Test Pattren</InputLabel>
                <Select labelId='test_pattren' id='test_pattren_select'
                    value={''} onChange={((e) => handleCommand(e.target.value as ControlKeys))}>
                    <MenuItem value={ControlCommands.TEST_PATTERN_OFF}>Off</MenuItem>
                    <MenuItem value={ControlCommands.TEST_PATTERN_WHITE}>White</MenuItem>
                    <MenuItem value={ControlCommands.TEST_PATTERN_BLACK}>Black</MenuItem>
                    <MenuItem value={ControlCommands.TEST_PATTERN_FOCUS_RED}>Focus Red</MenuItem>
                    <MenuItem value={ControlCommands.TEST_PATTERN_FOCUS_WHITE}>Focus White</MenuItem>
                </Select>

            </FormControl>
        </>)
}