import {ControlKeys, ControlCommands} from '../../util/constants'
import {FormControl, InputLabel, Select, MenuItem} from '@material-ui/core'
import { dropdownStyle } from './DropdownStyle'

interface Props{
    handleCommand: (e: ControlKeys) => void
}


const useStyles = dropdownStyle
export default function BackColor(props: Props){
    const {handleCommand} = props
    const classes = useStyles()

    return (
        <>
    <FormControl className={classes.formControl}>
    <InputLabel id='backcolor'>Back Color</InputLabel>
    <Select labelId='backcolor' id='backcolor'
     value={''} onChange={((e)=> handleCommand(e.target.value as ControlKeys))}>
         <MenuItem value={ControlCommands.BACK_COLOR_BLUE}>Blue</MenuItem>
         <MenuItem value={ControlCommands.BACK_COLOR_BLACK}>Black</MenuItem>
         <MenuItem value={ControlCommands.BACK_COLOR_DEFAULT_LOGO}>Default Logo</MenuItem>
         <MenuItem value={ControlCommands.BACK_COLOR_USER_LOGO}>User Logo</MenuItem>
     </Select>
     
</FormControl>
</> )
}