import {ControlKeys, ControlCommands, HandleCommand} from '../../util/constants'
import {FormControl, InputLabel, Select, MenuItem} from '@material-ui/core'
import { dropdownStyle } from './DropdownStyle'

interface Props{
    handleCommand: HandleCommand
}


const useStyles = dropdownStyle
export default function OSDRotation(props: Props){
    const {handleCommand} = props
    const classes = useStyles()

    return (
        <>
    <FormControl className={classes.formControl}>
    <InputLabel id='osdRotation'>OSD Rotation</InputLabel>
    <Select labelId='osdRotation' id='osdRotation'
     value={''} onChange={((e)=> handleCommand(e.target.value as ControlKeys))}>
         <MenuItem value={ControlCommands.OSD_ROTATION_OFF}>Off</MenuItem>
         <MenuItem value={ControlCommands.OSD_ROTATION_CLOCKWISE}>Clockwise</MenuItem>
         <MenuItem value={ControlCommands.OSD_ROTATION_COUNTERCLOCKWISE}>Counter Clockwise</MenuItem>
     </Select>
     
</FormControl>
</> )
}